@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}

body {
  background: #8758ff;
  display: flex;
  justify-content: center;
  align-items: center;
  /* overflow: hidden;  */
}

.todo-input {
  outline: none;
  background: none;
  border: 1px solid #8758ff;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #fff;
}

.todo-input::placeholder {
  color: #ffffff4d;
}

.todo-btn {
  background: #8758ff;
  color: #fff;
  border: none;
  padding: 0.55rem;
  cursor: pointer;
  
}

.TodoForm {
  width: 100%;
  margin-bottom: 1rem;
}

.TodoWrapper {
  background: #1A1A40;
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 5px;
  max-width: 500px;

}

.Todo {
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  background: #8758ff;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin-bottom: 1rem;
  cursor: pointer;
  position: relative;

}

.todoTitle{
  overflow: hidden;
  max-width: 300px;
}

.icoonss{
  right : 10px;
  position: absolute;
}


h1 {
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}

.App {
  text-align: center;
}

.completed {
  color: #c5aeff;
  text-decoration: line-through;
}

.fa-trash {
  margin-left: 0.75rem;
}
